import React, { Component } from 'react'
import { Modal } from 'react-bootstrap';

export default class manualsyncproductmodal extends Component {
  render() {
    const { show, handleClose, uploadCsv, onAddProduct} = this.props
    return (
        <Modal show={show} size="md" onHide={handleClose} dialogClassName="modal-dialog-centered upload_manual" id="manualupload">
            <Modal.Header>
                <Modal.Title>Sync Product</ Modal.Title>
                    <button type="button" className="close" data-bs-dismiss="modal" onClick={handleClose} >
                        <i aria-hidden="true" className="ki ki-close"></i>
                    </button>
            </Modal.Header>
            <Modal.Body className="py-20">
                <ul>
                    <li>
                        <button type="button" onClick={uploadCsv}>
                            <div className="icons">
                                <img src="https://s3.us-west-1.wasabisys.com/cloud.taggbox.com/taggshop/app/assets/media/display-logo/cvc.svg" alt="Add Single Product icon" />
                                <span>Upload CSV File</span>
                            </div>
                        </button>
                    </li>
                    <li>
                        <button type="button" data-bs-toggle="modal" data-bs-target="#addNewCustomProduct" onClick={onAddProduct}>
                            <div className="icons">
                            <img src="https://s3.us-west-1.wasabisys.com/cloud.taggbox.com/taggshop/app/assets/media/display-logo/add_product.svg" alt="add product icon" />
                            <span>Add Single Product</span>
                            </div>
                        </button>
                    </li>
                </ul>
            </Modal.Body>
        </Modal>
    )
  }
}