import React, { Component } from 'react'
import Swal from 'sweetalert2'
import { CLOUDPATH_TAGGSHOP } from '../../../../../actions/api'
import { TOAST_ERROR, TOAST_LOADING } from '../../../../../utils'


export default class productlist extends Component {

    
  deleteProduct=(product)=>{
    const { ACTION_PRODUCT_DELETE, authorized, countAllPage } = this.props
    Swal.fire({
        
        title: "Are you sure?",
        text: 'Are you sure you want to permanently delete this selected product?',
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "Yes, Delete it",
        cancelButtonText: "Cancel",
    }).then((result)=>{
        if (result.value) {
            TOAST_LOADING() 
            ACTION_PRODUCT_DELETE({id: `${product.id}`}, countAllPage)
        }
        else if (result.dismiss === "cancel") { 
            TOAST_ERROR('Your product is safe.')
        }       
    })
  }

 

  render() {
    const { product, onEditPost, onProductSelect,selectedProducts} = this.props 

    return ( 
        <tr>
            <td> 
                {/* <i className="far fa-square fa-f-24 select-icon text-dark-75" aria-hidden="true"></i> */}
                <input type='checkbox' checked={selectedProducts.includes(product?.id)} onChange={(e) => onProductSelect(product?.id, e.target.checked)} />
            </td>
            <td>
                <div className="tagg_list_post_img">
                    <div className="tagg_list_media_holder align-content-center d-flex flex-wrap">
                        <img className="img-fluid" src={product.product_image} alt="product icon" width="50px" height="50px" onError={e=> e.target.src=`${CLOUDPATH_TAGGSHOP}/app/assets/media/products/no-product.svg`} />
                    </div>
                </div>
            </td>
            <td>
                <span className="font-weight-bolder title">{product.product_title}</span>
            </td>
            <td>
                <span className="text-nowrap">{product.price_currency_symbol} {product.product_price}</span>
            </td>
            <td>
                <span className="text-nowrap">{product.price_currency_symbol} {product.product_discount}</span>
            </td>
            <td>
                <span className="back-strong">
                    <a href={product.product_url} title="Adidas Classic Backpack" target="_blank" className="text-dark-75 form-control product-url ng-binding">
                        {product.product_url}
                    </a>
                </span>
            </td>
            <td>
                <span className="text-dark-75 form-control product-url mb-0" style={{maxWidth:"100%"}}>{product.shopify_product_id}</span>
            </td>
            <td>
                <span>{product.shopify_product_type}</span>
            </td>
            <td>
                <span>{product.product_sku}</span>
            </td>

            <td>
                <span>{product.tags}</span>
            </td>
            <td className="text-center" style={{background:"#f5f5f5", position:"sticky", right:"0"}}>
                <button className="action-btn btn btn-clean btn-sm btn-icon" data-width="720px" tabIndex="-1" onClick={() => onEditPost(product)}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                        <g id="Group_4610" data-name="Group 4610" transform="translate(10956 -4501)">
                        <rect id="Rectangle_2068" data-name="Rectangle 2068" width="24" height="24" transform="translate(-10956 4501)" fill="none" opacity="0"></rect>
                            <g id="Group_3906" data-name="Group 3906" transform="translate(-10951.596 4505.376)">
                                <path id="Path_5042" data-name="Path 5042" d="M3.2,14.952,2.3,18.872a.985.985,0,0,0,.956,1.195,1.023,1.023,0,0,0,.206,0L7.4,19.158l7.571-7.543L10.746,7.4Z" transform="translate(-2.272 -4.732)" fill="#545454"></path>
                                <path id="Path_5043" data-name="Path 5043" d="M27.5,4.926l-2.82-2.82a.989.989,0,0,0-1.4,0L21.72,3.673l4.221,4.221,1.568-1.568a.989.989,0,0,0,0-1.4Z" transform="translate(-12.424 -1.817)" fill="#545454"></path>
                            </g>
                        </g>
                    </svg>
                </button>
                <button className="action-btn btn btn-clean btn-sm btn-icon" onClick={()=>this.deleteProduct(product)}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                        <g id="Group_4611" data-name="Group 4611" transform="translate(10956 -4533)">
                            <rect id="Rectangle_2069" data-name="Rectangle 2069" width="24" height="24" transform="translate(-10956 4533)" fill="none" opacity="0"></rect>
                            <g id="Group_4324" data-name="Group 4324" transform="translate(-10952 4536)">
                                <path id="Path_5188" data-name="Path 5188" d="M2.722,16.328A2,2,0,0,0,4.694,18h6.612a2,2,0,0,0,1.972-1.672L14,6H2ZM9,9.5A.5.5,0,0,1,9.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5Zm-4,0A.5.5,0,0,1,5.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5ZM15,2H11A2,2,0,0,0,9,0H7A2,2,0,0,0,5,2H1A1,1,0,0,0,1,4H15a1,1,0,0,0,0-2Z" fill="#545454" fillRule="evenodd"></path>
                            </g>
                        </g>
                    </svg>
                </button>
            </td>
        </tr>
    )
  }
}