import React, { useState } from 'react'
import { Modal } from 'react-bootstrap'
import { MAGENTO_TOKEN_VERIFY, SYNC_MAGENTO_PRODUCTS } from '../../../../../actions/productAction'
import { TOAST_ERROR } from '../../../../../utils'

export const AddMagentoModal = ({ show, onHide, refreshProducts, m_url }) => {
    const [m_token, setM_token] = useState('')
    const [m_stores, setM_stores] = useState(null)
    const [selectedStore, setSelectedStore] = useState('')

    const onVerifyToken = () => {
        var url = m_url
        if(!url.includes("https://") && !url.includes("http://") ){
            url = `https://${url}}`
        }
        MAGENTO_TOKEN_VERIFY({ accessToken: m_token, magentourl: url}).then(res => {
            const { status, storelist, message } = res.data
            if (status) {
                setM_stores(storelist)
                // if(storelist.length) setSelectedStore(storelist[0].code)
            }
            else TOAST_ERROR(message)
        })
    }
    const onSyncProduct = () => {
        SYNC_MAGENTO_PRODUCTS(selectedStore, m_token,m_url, onClose)
    }
    const onClose = () => {
        onHide()
        refreshProducts()
    }
    return (
        <Modal show onHide={onHide} size="lg" dialogClassName="modal-dialog-centered    " id="addNewCustomProduct">
            <Modal.Header>
                <Modal.Title>Connect Your Store</ Modal.Title>
                <button type="button" className="close" data-bs-dismiss="modal" onClick={onHide}>
                <i aria-hidden="true" className="ki ki-close"></i>
                </button>
            </Modal.Header>
            <Modal.Body>
                <div className="col-sm-12 col-lg-10 mb-5">
                    <p className="py-3">You are trying to connect the Store. Please Enter Access Token for your website.</p>
                    <h5>Access Token</h5>
                    <input name="data[accessToken]" required="required" className="form-control accessToken" value={m_token} onChange={e => setM_token(e.target.value)} id="accessToken" type="text" />
                    <small className="font-weight-bold">Note: Only Compatible with Magento 2.x</small>
                    <p className="py-3 mt-2">How to generate access Token. <a style={{ color: "#ec6737" }} href="https://devdocs.magento.com/guides/v2.4/get-started/authentication/gs-authentication-token.html" alt="How to generate access Token." target="_blank">Click here</a> </p>
                </div>
                {m_stores && m_stores.length ?
                    <div className="col-sm-12 col-lg-10 mb-5 showStoreList" >
                        <h5>Choose Store</h5>
                        <select name="data[storecode]" required="required" className="form-control" id="storesList" onChange={e => setSelectedStore(e.target.value)}>
                            <option value={''}>-- Choose Store --</option>

                            {m_stores.map(store => {
                                if (store.id != 0) {
                                    return <option value={store.code}>{store.name}</option>
                                }
                            })
                            }
                        </select>
                    </div>
                    : null}
            </Modal.Body>
            <Modal.Footer>
                <div className="d-flex align-items-center justify-content-end w-100">
                    <div className="card-toolbar">
                        {!m_stores ? <button type="submit" className="btn btn-primary addsync_magento_product synctBtn" id="addPreviewForm" onClick={onVerifyToken}>Verify Token</button>
                            : <button type="submit" className="btn btn-primary addsync_magento_product synctBtn" id="addPreviewForm" onClick={onSyncProduct}>Sync Product</button>}
                        {/* <button type="submit" className="btn btn-primary blackBtn" id="submitForm">Submit</button>
                        <button type="submit" className="btn btn-primary editBtn ml-4" id="addModeForm"> <span>Edit Mode</span> </button> */}
                    </div>
                </div>
            </Modal.Footer>
        </Modal>
    )
}
