                                                                        <path id="Path_5218" data-name="Path 5218" d="M21.083,18H2.917a.99.99,0,0,0-.984.982v.006a1,1,0,0,0,.985.981H21.083a.985.985,0,0,0,0-1.969Z" fill="#545454"></path>
