import React, { Component } from 'react'
import { Modal } from 'react-bootstrap';
import { DragDrop } from "@uppy/react";
import { TOAST_LOADING } from '../../../../../utils';
import { IMPORT_CSV_PRODUCT } from '../../../../../actions/productAction';
import UppyUpload from '../../../../../utils/uppyUpload';


export default class addNewProductImport extends Component {
    state = { selectedFile: "", filename: "", selected: "", uploadedFileName: '', isLoading: false, loading: false }
    uppyUploader = new UppyUpload(['.csv'])

    componentDidMount() {
        this.uppyUploader.uppy.on('transloadit:result', (stepName, result) => {
            this.setState({ selectedFile: result.id, filename: `https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`, selected: `https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`, uploadedFileName: result.name }, () => {
                setTimeout(() => { this.uppyUploader.uppy.cancelAll() }, 300)
            })
            this.setState({loading: false})

        }) 

        this.uppyUploader.uppy.on('file-added', (file) => {
            // console.log('Added file', file);
            this.setState({loading: true})
        });
    }

    onUploadCSV = () => {
        TOAST_LOADING()
        var obj = new FormData()
        obj.append("csv_url", this.state.filename) 
        IMPORT_CSV_PRODUCT(this.state.filename, this.props.onHide, this.props.countAllPage)

    }
    onClose = () => {
        this.setState({ selectedFile: "", filename: "", selected: "", uploadedFileName: '' }, () => this.props.onHide())
        
    }

    render() {
        const { show, onHide } = this.props
        const {loading, uploadedFileName} = this.state
        return (
            <Modal show onHide={this.onClose} size="md" dialogClassName="modal-dialog-centered upload_manual" id="addNewProductImport">
                <Modal.Header>
                    <Modal.Title>Import Products by CSV</ Modal.Title>
                    <button type="butto,n" className="close" data-bs-dismiss="modal" onClick={this.onClose} >
                        <i aria-hidden="true" className="ki ki-close"></i>
                    </button>
                </Modal.Header>
                <Modal.Body>
                    <div className="row">
                        <div className="col-md-12">
                            <p>Download the&nbsp;
                                <a href="https://cloud.taggbox.com/taggshop/app/assets/media/sample-products.csv" target="_blank" className="text-link" download="">Sample CSV template&nbsp;</a>
                                to see an example of the format required
                            </p>

                            
                                 <DragDrop
                                    proudlyDisplayPoweredByUppy={false} 
                                    hideUploadButton={true}
                                    showProgressDetails={true}
                                    disableThumbnailGenerator={false}
                                    height={250}
                                    // browserBackButtonClose={true}
                                    showRemoveButtonAfterComplete={false}
                                    uppy={this.uppyUploader.uppy} 
                                />
                            <div className="selected-file mt-2">{uploadedFileName == '' ? 'Your selected file name': ''} {uploadedFileName != '' ? uploadedFileName: null} </div> 
                        </div>
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <div className="d-flex align-items-center justify-content-end w-100">
                        <div className="card-toolbar">
                            <button type="button" className="btn btn-secondary mr-4" onClick={this.onClose}>Cancel</button>
                            <button type="button" className={`btn btn-primary ${uploadedFileName == '' ? 'disabled' : ''}`} id="uploadProductCsv" onClick={this.onUploadCSV}>Upload and continue</button>
                        </div>
                    </div>
                </Modal.Footer>
            </Modal>
        )
    }
}