import React, { Component } from 'react'

export default class Syncproductcatalogue extends Component {
  render() {
    const {openManualSyncModal, openSyncProduct} = this.props
    return (
        <div className="syncproduct_catalogue">
            <div className="d-flex flex-column-fluid main_content">
                {/* <div className="loader-overlay">
                    <div className="d-flex align-items-center justify-content-center h-100 w-100">
                        <img className="img-fluid" height="72" width="72" src="https://cloud.taggbox.com/taggshop/app/assets/media/logos/taggshop-preloader.gif" alt="loader" />
                    </div>
                </div> */}
                <div className="tagg_pro_wrapper">
                    <div className="container-fluid">
                        <div className="view_products_wrap" id="product_landing">
                            <div className="row">
                                <div className="card card-custom w-100 rounded-0">
                                    <div className="card-header flex-wrap py-3 add_product_wt">
                                        <div className="card-title m-0">
                                            <h3 className="m-0">Product List</h3>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="products_view mt-20">
                                <div className="small_container">
                                    <div className="row justify-content-center align-items-center py-5">
                                        <div className="col-xl-12 mb-5">
                                            <div className="product_sync_title mb-18">
                                                <h3 className="font-weight-bolder text-dark">Sync Product Catalogue</h3>
                                                <p>Select the application from which you want to tag products</p>
                                            </div>
                                            <div className="wrap_product">
                                                <ul>
                                                    <li onClick={() => openSyncProduct('shopify')}>
                                                        <a href="#" className="btn popUp" id="shopifyProductb">
                                                            <div>
                                                                <img src="https://cloud.taggbox.com/taggshop/app/assets/media/display-logo/shopify.svg" alt="shopify-icon" />
                                                                <span>Shopify</span>
                                                            </div>
                                                        </a>
                                                    </li>
                                                    <li onClick={() => openSyncProduct('google')}>
                                                        <a href="#" className="btn popUp" id="googleProductb">
                                                            <div>
                                                                <img src="https://cloud.taggbox.com/taggshop/app/assets/media/display-logo/google-merchant-center.svg" alt="google-merchant-center-icon" />
                                                                <span>Google Merchant Center</span>
                                                            </div>
                                                        </a>
                                                    </li>
                                                    <li onClick={() => openSyncProduct('bmc')}>
                                                        <a href="#" className="btn popUp" id="bigCommerceProductb">
                                                            <div>
                                                                <img src="https://cloud.taggbox.com/taggshop/app/assets/media/display-logo/bigcommerce.svg" alt="bigcommerce-icon" />
                                                                <span>BigCommerce</span>
                                                            </div>
                                                        </a>
                                                    </li>
                                                    <li onClick={() => openSyncProduct('magento')}>
                                                        <a href="#" className="btn popUp" id="magentoProductb">
                                                            <div>
                                                                <img src="https://cloud.taggbox.com/taggshop/app/assets/media/display-logo/magento_icon.svg" alt="Magento-icon" />
                                                                <span>Magento (Beta)</span>
                                                            </div>
                                                        </a>
                                                    </li>
                                                    <li onClick={() => openSyncProduct('woocommerce')}>
                                                        <a href="#" className="btn popUp" id="woocommerceProductb">
                                                            <div>
                                                                <img src="https://cloud.taggbox.com/taggshop/app/assets/media/display-logo/woocommerce.svg" alt="Magento-icon" />
                                                                <span>Woocommerce (Beta)</span>
                                                            </div>
                                                        </a>
                                                    </li>
                                                    <li>
                                                        <button type="button" onClick={() => openManualSyncModal()}>
                                                            <div>
                                                                <img src="https://cloud.taggbox.com/taggshop/app/assets/media/display-logo/add.svg" alt="add-icon" />
                                                                <span>Manual Upload</span>
                                                            </div>
                                                        </button>
                                                    </li>
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
  }
}